body {
  font-family: avenir,sans-serif;
}
.video_wrapper {
  width: 100%;
  height: 100%;
  margin: 0 auto;
  position: relative;
  background-color: #000;
}
.video_wrapper_full {
  padding-top: 315px;
}
.videoIframe {
  position: absolute;
  top: 0;
  bottom: 0;
  right: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background-color: transparent;
}
.videoPoster {
  position: absolute;
  top: 0px;
  left: 0px;
  width: 100%;
  height: 100%;
  background-image:url(../img/yt/main_yt.png);
  background-size: cover;
  border: none;
  overflow: hidden;
  opacity: 1;
  -webkit-transition: opacity 800ms, height 0s;
  -moz-transition: opacity 800ms, height 0s;
  transition: opacity 800ms, height 0s;
  -webkit-transition-delay: 0s, 0s;
  -moz-transition-delay: 0s, 0s;
  transition-delay: 0s, 0s;
  display: -webkit-flex;
  display: -moz-flex;
  display: -ms-flex;
  display: -o-flex;
  display: flex;
  justify-content: center;
  -ms-align-items: center;
  align-items: center;
}
.videoPoster:hover {
  cursor: pointer;
}

.videoPoster:after {
  content: url(../img/yt/play_btn.png);
  position: absolute;

}
